.register {
  width: 100vw;
}
.register img {
  position: relative;
  width: 100vw;
  height: 100vh;
}
.register form {
  position: absolute;
  border: 0.1vw solid rgb(105, 105, 105);
  border-radius: 1vw;
  box-shadow: 2vw 2vh 2vw rgb(177, 176, 176);
  margin: -95vh 0 0 10vw;
  width: 40vw;
}
.registerForm h1 {
  padding: 1vh 0 0vh 14vw;
  font-size: 3vw;
  font-weight: bold;
  font-style: italic;
}
.registerEmail,
.registerName,
.registerPhone,
.registerPassword {
  padding: 2vh 1vw 2vh 4vw;
}
.registerEmail input,
.registerPassword input,
.registerName input,
.registerPhone input {
  font-size: 1.4vw;
  width: 30vw;
  padding: 2vh 1vw 2vh 1vw;
}
.registerForm button {
  width: 32vw;
  padding: 2.5vh 1vw 2.5vh 1vw;
  font-size: 1.4vw;
  background-color: rgb(54, 53, 53);
  color: white;
  font-style: italic;
  border-radius: 0.5vw;
}
.registerButton {
  padding: 2vh 0 2vh 4vw;
  width: 30vw;
}
.registerForm button:hover,
.registerForm button:active {
  box-shadow: 5px 5px 5px rgb(102, 100, 100);
}
.loginUp {
  font-size: 1.6vw;
  font-style: italic;
  padding: 0 0 5vh 15vw;
}
.loginUp a {
  color: brown;
  font-weight: bold;
}
.loginUp a:hover,
.loginUp a:active {
  background-color: brown;
  color: white;
  padding: 1vh 1vw 1.5vh 1vw;
  border-radius: 0.5vw;
}
